<script setup lang="ts">
import { useMenuStore } from '~/store/menu'
import { useThemeStore } from '~/store/theme'

const menuStore = useMenuStore()
const themeStore = useThemeStore()

const toggleMenu = () => {
  if (menuStore.isMenuOpen) {
    menuStore.closeMenu()
  } else {
    menuStore.openMenu()
  }
}
</script>

<template>
  <div
    class="burger-menu"
    :class="{ 'burger-menu--dark': themeStore.isDarkTheme }"
  >
    <button
      class="burger-menu__btn"
      :class="{ active: menuStore.isMenuOpen }"
      @click="toggleMenu"
    >
      <span />
      <span />
      <span />
    </button>
  </div>
</template>

