<script setup lang="ts">
import { ref } from 'vue'
import { useI18n } from 'vue-i18n'

import { useLocale } from '~/composables/useLocale'
import type { LocalesCodesEnum } from '~/enums/locales'
import { useThemeStore } from '~/store/theme'

const localeDropdown = ref(null)
const { localesList, isLocale, openLocales, closeLocales } =
  useLocale(localeDropdown)
const { locale, setLocale } = useI18n()
const themeStore = useThemeStore()

const onChangeLocale = (code: LocalesCodesEnum) => {
  setLocale(code)
  closeLocales()
}
</script>

<template>
  <div class="locale" :class="{ 'locale--dark': themeStore.isDarkTheme }">
    <div class="locale-lang">
      {{ $t('locale.language') }}
      <div class="locale-lang__current" @click="openLocales">
        {{ localesList[locale].title }}
        <span class="icon-chevron icon-chevron--orange icon-chevron--small" />
      </div>
    </div>

    <div v-if="isLocale" ref="localeDropdown" class="locale-list">
      <div class="locale-list__divider" />
      <div
        v-for="lang in Object.values(localesList)"
        :key="lang.code"
        class="locale-list__item"
        :class="{ active: lang.code === locale }"
        @click="onChangeLocale(lang.code)"
      >
        {{ lang.title }}
      </div>
    </div>
  </div>
</template>

