import { defineStore } from 'pinia'

import { addNoScrollBody, removeNoScrollBody } from '~/utils/user-actions'

type TMenuState = {
  isMenuOpen: boolean
}

export const useMenuStore = defineStore('menu', {
  state: (): TMenuState => ({
    isMenuOpen: false,
  }),
  getters: {},
  actions: {
    openMenu() {
      this.isMenuOpen = true
      addNoScrollBody()
    },
    closeMenu() {
      this.isMenuOpen = false
      removeNoScrollBody()
    },
  },
})
