<script setup lang="ts">
import { ref } from 'vue'
import { useI18n } from 'vue-i18n'

import IconEarth from '~/components/icons/IconEarth.vue'
import { useLocale } from '~/composables/useLocale'
import type { LocalesCodesEnum } from '~/enums/locales'
import { useThemeStore } from '~/store/theme'

const localeDropdown = ref(null)
const { localesList, isLocale, openLocales, closeLocales } =
  useLocale(localeDropdown)
const { locale, setLocale } = useI18n()
const themeStore = useThemeStore()

const onChangeLocale = (code: LocalesCodesEnum) => {
  setLocale(code)
  closeLocales()
}
</script>

<template>
  <div
    class="header-lang"
    :class="{ 'header-lang--dark': themeStore.isDarkTheme }"
  >
    <div
      class="header-lang__current"
      :class="{ active: isLocale }"
      @click="openLocales"
    >
      <IconEarth :is-dark-theme="themeStore.isDarkTheme" />
      {{ localesList[locale].title }}
      <span
        class="icon-chevron"
        :class="{ 'icon-chevron--dark': !themeStore.isDarkTheme }"
      />
    </div>

    <div v-if="isLocale" ref="localeDropdown" class="header-lang-list">
      <div
        v-for="lang in Object.values(localesList)"
        :key="lang.code"
        class="header-lang-list__item"
        :class="{ active: lang.code === locale }"
        @click="onChangeLocale(lang.code)"
      >
        {{ lang.title }}
      </div>
    </div>
  </div>
</template>

