import { defineStore } from 'pinia'

type TThemeState = {
  isDarkTheme: boolean
}

export const useThemeStore = defineStore('theme', {
  state: (): TThemeState => ({
    isDarkTheme: false,
  }),
  getters: {},
  actions: {
    setDarkTheme() {
      this.isDarkTheme = true
    },
    removeDarkTheme() {
      this.isDarkTheme = false
    },
  },
})
