import { ref } from 'vue'
import { onClickOutside } from '@vueuse/core'
import { LocalesCodesEnum } from '~/enums/locales'

const localesList = {
  [LocalesCodesEnum.EN]: {
    code: LocalesCodesEnum.EN,
    title: 'English',
  },
  [LocalesCodesEnum.RU]: {
    code: LocalesCodesEnum.RU,
    title: 'Russian',
  },
}

export const useLocale = (outsideRef) => {
  const isLocale = ref(false)

  const openLocales = () => {
    isLocale.value = true
  }

  const closeLocales = () => {
    isLocale.value = false
  }

  onClickOutside(outsideRef, () => {
    closeLocales()
  })

  return {
    localesList,
    isLocale,
    openLocales,
    closeLocales,
  }
}
