<script setup lang="ts">
import { useLocaleHead } from '#i18n'
import { useHead } from 'unhead'

import Header from '~/components/app/Header/Header.vue'
import Footer from '~/components/app/Footer/Footer.vue'
import { useDarkThemeProvider } from '~/composables/useDarkTheme'
import { useMobileMenuProvider } from '~/composables/useMobileMenu'

const i18nHead = useLocaleHead({
  addDirAttribute: true,
  identifierAttribute: 'id',
  addSeoAttributes: true,
})

useHead({
  htmlAttrs: {
    lang: i18nHead.value.htmlAttrs!.lang,
    dir: i18nHead.value.htmlAttrs.dir,
  },
  link: [...(i18nHead.value.link || [])],
  meta: [...(i18nHead.value.meta || [])],
})

useDarkThemeProvider()
useMobileMenuProvider()
</script>

<template>
  <Header />
  <main class="main">
    <NuxtPage />
  </main>
  <Footer />
</template>

<style lang="scss" scoped>
.main {
  margin-top: var(--header-height);
}
</style>
